<template>
    <div class="novel-link">
        <el-button @click="add()" type="primary" v-if="!isShow" style="margin-bottom: 30px;"></el-button>
        <label style="margin-left: 30px;" v-if="!isShow"><el-input v-model="tokenArr" style="width:300px;"></el-input></label>
        <el-button :disabled="tokenArr == ''" @click="updateTokens()" type="success" v-if="!isShow"></el-button>
        <el-table :data="tableData" border style="width: 100%;" v-if="!isShow">
            <el-table-column align="center" header-align="center" prop="id" label="id" width="60"></el-table-column>
            <el-table-column align="center" header-align="center" prop="type" label="" width="100">
                <template slot-scope="scope">
                    {{scope.row.type == 0 ? '' : (scope.row.type == 1 ? '' : '')}}
                </template>
            </el-table-column>
            <el-table-column align="center" header-align="center" prop="name" label="" :width="80"></el-table-column>
            <el-table-column align="center" header-align="center" prop="picurl" label="" width="200">
                <template slot-scope="scope">
                    <img class="beauty" :src="domain_names[0].domain_name + scope.row.picurl" />
                </template>
            </el-table-column>
            <el-table-column header-align="center" prop="channel" label="" width="100"></el-table-column>
            <el-table-column header-align="center" prop="remarks" label=""></el-table-column>
            <el-table-column align="center" header-align="center" label="" width="120">
                <template slot-scope="scope">
                    <el-button v-if="scope.row.type == 0" :data-clipboard-text="domain_names[0].domain_name + '/tuiguang/weitiao/' + scope.row.id" class="copyBtn" @click="copy" type="success" size="small"></el-button>
                    <el-button v-if="scope.row.type == 1" :data-clipboard-text="domain_names[0].domain_name + '/tuiguang/singlepage/' + scope.row.id" class="copyBtn" @click="copy" type="success" size="small"></el-button>
                    <el-button v-if="scope.row.type == 2" :data-clipboard-text="domain_names[0].domain_name + '/tuiguang/multipage/' + scope.row.id" class="copyBtn" @click="copy" type="success" size="small"></el-button>
                </template>
            </el-table-column>
            <el-table-column align="center" header-align="center" label="" width="150">
                <template slot-scope="scope">
                    <el-button @click="update(scope.row)" type="primary" size="small"></el-button>
                    <el-button @click="deleteOne(scope.row)" type="danger" size="small"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-form ref="form" :model="form" label-width="100px" size="mini" v-if="isShow">
            <el-form-item label="">
                 <el-radio-group v-model="form.type">
                    <el-radio :label="0"></el-radio>
                    <el-radio :label="1"></el-radio>
                    <el-radio :label="2"></el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item label="id">
                <el-input :disabled="isUpdate" v-model="form.id" style="width:100px;"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="form.pageTitle" style="width:500px;"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="form.articleTit" style="width:500px;"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="form.name" style="width:500px;"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="form.desc" style="width:500px;"></el-input>
            </el-form-item>
            <el-form-item label="" prop="imageFile">
                <el-upload :show-file-list="false" enctype="multipart/form-data" name="imageFile" action="http://www.rrdtjj.top/tuiguang/novel/upload" :on-success="handleAvatarSuccess">
                    <el-button size="small" type="primary"></el-button>
                    <img class="b_img" v-if="imgUrl" :src="imgUrl" alt="" width="200">
                </el-upload>
            </el-form-item>
            <el-form-item label="" prop="content">
                <el-input type="textarea" v-model="form.capter1" style="width:500px;"></el-input>
            </el-form-item>
            <el-form-item v-if="form.type == 2" label="()" prop="content">
                <el-input type="textarea" v-model="form.capter2" style="width:500px;"></el-input>
            </el-form-item>
            <el-form-item v-if="form.type == 0" label="">
                <el-input v-model="form.linkUrl" style="width:500px;"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="form.statisticsUrl1" style="width:500px;"></el-input>
            </el-form-item>
            <el-form-item v-if="form.type == 2" label="()">
                <el-input v-model="form.statisticsUrl2" style="width:500px;"></el-input>
            </el-form-item>
            <el-form-item v-if="form.type != 1" label="">
                <el-input v-model="form.tokenCodes" style="width:500px;"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="form.channel" style="width:150px;"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="form.remarks" style="width:500px;"></el-input>
            </el-form-item>
            <el-form-item size="large">
                <el-button v-if="!isUpdate" type="primary" @click="create"></el-button>
                <el-button v-else type="primary" @click="save"> </el-button>
                <el-button v-if="!isUpdate" type="info" @click="reset"> </el-button>
                <el-button type="danger" @click="cancel"> </el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import Axios from 'axios'
    import Clipboard from 'clipboard';
    let clipboard = new Clipboard('.copyBtn');
    export default {
        data() {
            return {
                form: {
                    type: 0,
                    pageTitle: '',
                    articleTit: ' ',
                    desc: ''
                },
                imgUrl: '',
                isShow: false,
                tableData: [],
                isUpdate: false,
                domain_names: [],
                tokenArr: ''
            };
        },
        mounted() {
            this.show()
        },
        methods: {
            updateTokens() {
                this.$confirm('?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    Axios({
                        url: '/tuiguang/token_arr',
                        method: 'get',
                        params: {
                            tokenArr: this.tokenArr
                        }
                    }).then(res => {
                        this.$message({
                            type: 'success',
                            message: res.data.success
                        })
                        this.tokenArr = ''
                    })
                }).catch(() => {});
                
            },
            getContent(id) {
                Axios({
                    url: '/tuiguang/novel/get_content',
                    method: 'get',
                    params: {
                        _id: id
                    }
                }).then(res => {
                    this.$set(this.form,'capter1',res.data.data.capter1)
                    this.$set(this.form,'capter2',res.data.data.capter2)
                })
            },
            add() {
                this.isShow = true
            },
            handleAvatarSuccess(res, file) {
                this.imgUrl = this.domain_names[0].domain_name + '/images/tuiguang/' + res.filename
                this.form.picurl = '/images/tuiguang/' + res.filename
            },
            create() {
                this.$confirm('/?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    Axios({
                        url: '/tuiguang/novel/add',
                        method: 'post',
                        data: {
                            type: this.form.type,
                            id: this.form.id,
                            pageTitle: this.form.pageTitle,
                            articleTit: this.form.articleTit,
                            name: this.form.name,
                            desc: this.form.desc,
                            picurl: this.form.picurl,
                            capter1: this.form.capter1,
                            capter2: this.form.capter2,
                            linkUrl: this.form.linkUrl,
                            statisticsUrl1: this.form.statisticsUrl1,
                            statisticsUrl2: this.form.statisticsUrl2,
                            channel: this.form.channel,
                            tokenCodes: this.form.tokenCodes,
                            remarks: this.form.remarks,
                        }
                    }).then(res => {
                        if(res.data.err) {
                            this.$message.error(res.data.err)
                        } else {
                            this.$message({
                                type: 'success',
                                message: res.data.message
                            })
                            this.form = {
                                type: 0,
                                pageTitle: '',
                                articleTit: ' ',
                                desc: ''
                            }
                            this.imgUrl = ''
                            this.isShow = false
                            this.show()
                        }
                    })
                }).catch(() => {});
                
            },
            reset() {
                this.$confirm('/?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.form = {
                        type: 0,
                        pageTitle: '',
                        articleTit: ' ',
                        desc: ''
                    }
                }).catch(() => {});
            },
            deleteOne(row) {
                this.$confirm('/?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    Axios({
                        url: '/tuiguang/novel/delete_one',
                        method: 'post',
                        data: {
                            id: row.id
                        }
                    }).then(res => {
                        this.$message({
                            type: 'success',
                            message: res.data.message
                        })
                        this.show()
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '~~~'
                    })
                });
            },
            copy() {
                this.$message({
                    type: 'success',
                    message: ''
                })
            },
            show() {
              Axios({
                url: '/tuiguang/novel/show',
                method: 'get'
              }).then(res => {
                  this.tableData = res.data.data
                  this.domain_names = res.data.domain_names
              })
            },
            update(row) {
                this.form = row
                this.isUpdate = this.isShow = true
                this.imgUrl = this.form.picurl
                this.getContent(row._id)
            },
            cancel() {
                this.isShow = this.isUpdate = false
                this.form = {
                    type: 0,
                    pageTitle: '',
                    articleTit: ' ',
                    desc: ''
                }
                this.imgUrl = ''
            },
            save() {
                this.$confirm('/?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    Axios({
                        url: '/tuiguang/novel/update',
                        method: 'post',
                        data: {
                            _id: this.form._id,
                            type: this.form.type,
                            id: this.form.id,
                            pageTitle: this.form.pageTitle,
                            articleTit: this.form.articleTit,
                            name: this.form.name,
                            desc: this.form.desc,
                            picurl: this.form.picurl,
                            capter1: this.form.capter1,
                            capter2: this.form.capter2,
                            linkUrl: this.form.linkUrl,
                            statisticsUrl1: this.form.statisticsUrl1,
                            statisticsUrl2: this.form.statisticsUrl2,
                            tokenCodes: this.form.tokenCodes,
                            channel: this.form.channel,
                            remarks: this.form.remarks,
                        }
                    }).then(res => {
                        this.$message({
                            type: 'success',
                            message: res.data.success
                        })
                        this.form = {
                            type: 0,
                            pageTitle: '',
                            articleTit: ' ',
                            desc: ''
                        }
                        this.imgUrl = ''
                        this.isShow = this.isUpdate = false
                    })
                }).catch(() => {});
                
            }
        }
    };
</script>

<style scoped>
.beauty {
    width: 180px;
}

.b_img {
    margin-top: 10px;
}
</style>




